.digital{
    margin-top: 10%;
    width: 100%;
    height: 80%;
}
.clock-text{
    display: flex;
    justify-content: center;
}

.clock-text-hour,
.clock-text-minutes
{
    font-size: var(--biggest-font-size);
    font-weight: var(--font-medium);
    color: var(--title-color);
}
.clock-text-ampm{
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-left: var(--mb-0-20); 
}

.clock-date{
    text-align: center;
    font-size: var(--small-fontlsize);
    font-weight: var(--font-medium);
}

.dayNow{
    font-size: var(--biggest-font-size);
    font-weight: var(--font-medium);
    color: var(--title-color);
    justify-content: center;
    display: flex;
}
.setAlarmOption{
    justify-content: center;
    display: flex;
    margin-top: 70%;
}