  .clock-circle{
    position:relative;
    margin-top: 5%;
    margin-left: 20%;
    width:80vh;
    height:80vh;
    box-shadow:-6px -6px 16px var(--white-color),
    6px 6px 16px hsla(var(--hue-color), 30%, 86%, 1),
    inset 6px 6px 16px hsla(var(--hue-color), 30%, 86%, 1),
    inset -6px -6px 16px var(--white-color);
    border-radius: 50%;
    display: flex;
    justify-content: center;
  }

    .alarm-icon {
    position: absolute;
    font-size: 45px;
    margin-left: 10px;
    
  }
  .active {
    animation: alarm 2s infinite alternate;
  } 
  
  .clock-twelve,
  .clock-three,
  .clock-six,
  .clock-nine {
    position: relative;
    width: 1rem;
    height: 1px;
    background-color: var(--first-color);
  }
  .clock-twelve,
  .clock-six {
    transform: translateX(-50%) rotate(90deg);
  } 
  .clock-number{
    position: absolute;
    font-size: 1rem;
    transform: translate(-50%,-50%);
  }
   .clock-twelve {
    top: 1.5rem;
    left: 50%;
  }
  .clock-three {
    top: 50%;
    right: 0.25rem;
  }
  .clock-six {
    bottom: 1.75rem;
    left: 50%;
  }
  .clock-nine {
    left: 1.25rem;
    top: 50%;
  }
  
   .clock-rounder {
    padding:5px 5px;
    margin-top: 49%;
    width:1px;
    height: 1px;
    position: absolute;
    background-color: var(--first-color);
    border-radius: 50%;
    border: 1px solid var(--body-color);
    z-index: var(--z-tooltip);
  }
  
  .clock-hour,
  .clock-minutes,
  .clock-seconds {
    position: absolute;
    padding: 50px;
    display: flex;
    justify-content: center;

  }
  
  .clock-hour {
    margin-top: 38%;
    width: 105px;
    height: 140px;
  }
  
  .clock-hour::before {
    content: '';
    position: absolute;
    background-color: var(--text-color);
    width: 0.25rem;
    height: 12rem;
    border-radius: 0.75rem;
    z-index: var(--z-normal);
  }
  
  .clock-minutes {
    margin-top: 34%;
    width: 10px;
    height: 186px;
  }
  .clock-minutes::before {
    content: '';
    position: absolute;
    background-color: var(--text-color);
    width: 0.25rem;
    height: 15rem;
    border-radius: 0.75rem;
    z-index: var(--z-normal);
  }
  .clock-seconds {
    margin-top: 33%;
    width: 10px;
    height: 200px;
  }
  .clock-seconds::before {
    content: '';
    position: absolute;
    background-color: var(--first-color);
    width: 0.125rem;
    height: 18rem;
    border-radius: 0.75rem;
    z-index: var(--z-normal);
  }
  
