.option-container {
  position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .wrapper-option {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  .disable {
    pointer-events: none;
  }
  .wrapper-option select {
    padding: 10px 15px;
    border-radius: 5px;
    font-size: 16px;
    text-align: center;
    border: none;
    outline: none;
    background-color: var(--body-color);
    box-shadow: -3px -3px 5px var(--white-color),
      3px 3px 5px hsla(var(--hue-color), 30%, 86%, 1),
      inset 3px 3px 5px hsla(var(--hue-color), 30%, 86%, 1),
      inset -3px -3px 5px var(--white-color);
    cursor: pointer;
  }
  select::-webkit-scrollbar {
    width: 8px;
    color: aliceblue;
  }
  select::-webkit-scrollbar-thumb {
    background:#787676;
    border-radius: 8px;
    border-right: 2px solid #fff;
  }
  select option {
    display: block;
    color:black;
  }
  .setAlarm-btn {
    margin-left: -320px; /* Space between the select and button */
    width: 320px; /* Button width */
    margin-top: 100px; /* Space from the top */
    height: 40px; /* Button height */
    border-radius: 5px; /* Rounds the corners */
    color: #fff; /* Text color */
    font-size: 16px; /* Font size */
    letter-spacing: 1px; /* Space between letters */
    background-color: var(--first-color); /* Background color */
    cursor: pointer; /* Change cursor to pointer */
  }

  .play {
    background-color: orange;
    color: #000;
  }